import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';

import Images from '../../components/form/images';

function byDate(a, b) {
	if (a.date < b.date){
		return -1;
	}
	if (a.date > b.date){
		return 1;
	}
	return 0;
}

const initialState = {
    newHistory: {},
	showLog: false,
};

@observer
class DogsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadDog() {
		const { dogStore } = this.props.stores;
        dogStore.load(this.props.dog);
    }

	resetLog() {
		this.setState({ ...initialState });
	}

	toggleLog = () => {
		const { showLog } = this.state;
		this.setState({
            showLog: !showLog,
        });
	}

	addImages = (obj) => {
		// updateField({ id: object.id, field, value: file });
		// console.log('addImages', obj.value);
		if (Array.isArray(obj.value)) {
			const { newHistory } = this.state;
			newHistory.images = obj.value;
			this.setState({ newHistory });
		}
	}

	insertLog = async () => {
		const { newHistory } = this.state;
		const { dogStore } = this.props.stores;
		const { dog } = dogStore;

		if (!newHistory.title && !newHistory.comment) {
			return false;
		}

		await dogStore.save({
			id: dog.id,
			history: {
				...newHistory,
			},
		});
        this.resetLog();
		this.toggleLog();
		this.loadDog();
	}

    back = () => {
        route(`/dogs/`, true);
    }

    componentWillMount() {
        this.loadDog();
    }

	render() {
		const { newHistory, showLog } = this.state;
		const { dogStore, userStore } = this.props.stores;
		const { dog } = dogStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Hund</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
						<div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
                                {!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3 pr-2'
								style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important;`}
							>
								<span class='float-right'>
									{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
									{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
								</span>
								<h5>{dog.name}</h5>
								{dog.shortname}

								<small>
									{dog.chipdId && <small>{dog.chipId}</small>}
								</small>
                            </div>
                        </div>

						<div class='container-fluid'>
							<div class='row'>
								{dog.images && dog.images.map(img => (
									<div class={`text-center border rounded imageRounded`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}>
										&nbsp;
									</div>
								))}
							</div>
						</div>

						<div class='container-fluid'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Fdt</small>
                                    <h3>
                                        {util.isoDate(dog.birth, false, false, true)}
                                    </h3>
                                </div>
                                {dog.deceased && <div class='col text-center'>
                                    <small class='text-muted'>Dd</small>
                                    <h3>
                                        {util.isoDate(dog.deceased, false, false, true)}
                                    </h3>
                                </div>}
                            </div>
	                        <div class='row mt-2'>
								<div class='col text-center'>
                                    <small class='text-muted'>Team</small>
                                    <h3>
                                        {dog.team && userStore.findTeam(dog.team)}
                                    </h3>
                                </div>
							</div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Mor</small>
                                    <h3>
                                        {dog.mother}
                                    </h3>
                                </div>
                                <div class='col text-center'>
                                    <small class='text-muted'>Far</small>
                                    <h3>
                                        {dog.father}
                                    </h3>
                                </div>
                            </div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Ssken</small>
                                    <h3>
                                        {dog.siblings}
                                    </h3>
                                </div>
                            </div>
                        </div>

						<h5 class='mt-3'>Logg for {dog.name}</h5>
						<div class='container-fluid'>
	                        {dog.history && dog.history.sort(byDate).reverse().map(line => (
								<div class='row mt-4'>
									<div class='col'>

										<div class='container-fluid'>
											<div class='row'>
												{line.images && line.images.map(img => (
													<div class={`text-center border rounded imageRounded imageRoundedLarge`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}>
														&nbsp;
													</div>
												))}
											</div>
										</div>

										<h5 class='m-0'>{line.title}</h5>
										<small class='text-muted'>{util.isoDate(line.date, false, false, true)}</small><br />
										{line.comment}
										<span class='float-right'>
											{line.cost ? `Kr. ${util.format(line.cost, 2)}` : ''}
										</span>
									</div>
								</div>
							))}
						</div>

						{showLog && <div>
							<h5>Legg til logg</h5>
							<div class='form-group'>
								<label for='dateInput'>Date</label>
								<input type='date' class='form-control' id='dateInput' aria-describedby='nameHelp'
									onInput={linkState(this, 'newHistory.date')}
									value={newHistory.date}
									autocomplete='off'
								/>
								<small id='nameHelp' class='form-text text-muted'>Tittel p logglinje. Vet besk, Vaksinering, Syk, etc.</small>
							</div>
							<div class='form-group'>
								<label for='nameInput'>Navn</label>
								<input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
									onInput={linkState(this, 'newHistory.title')}
									value={newHistory.title}
									autocomplete='off'
								/>
								<small id='nameHelp' class='form-text text-muted'>Tittel p logglinje. Vet besk, Vaksinering, Syk, etc.</small>
							</div>
							<div class='form-group'>
								<label for='commentInput'>Kommentar</label>
								<input type='text' class='form-control' id='commentInput' aria-describedby='nameHelp'
									onInput={linkState(this, 'newHistory.comment')}
									value={newHistory.comment}
									autocomplete='off'
								/>
								<small id='nameHelp' class='form-text text-muted'>Kommentar p logglinje.</small>
							</div>
							<div class='form-group'>
								<label for='costInput'>Kostnad</label>
								<input type='text' class='form-control' id='costInput' aria-describedby='nameHelp'
									onInput={linkState(this, 'newHistory.cost')}
									value={newHistory.cost}
									autocomplete='off'
								/>
								<small id='nameHelp' class='form-text text-muted'>Kostnad.</small>
							</div>

							<div class='container-fluid'>
								<div class='row'>
									{newHistory.images && newHistory.images.map(img => (
										<div class={`text-center border rounded imageRounded`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}>
											&nbsp;
										</div>
									))}
								</div>
							</div>
							<Images object={newHistory} field='images' updateField={this.addImages} />

							<button type='button' class='btn btn-success'
								onClick={this.insertLog}
							>
								Legg til logg
							</button>
						</div>}


						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href={`/dogs/edit/${dog.id}`}
							>
								<i class='fas fa-edit' /> Endre {dog.name}
							</a>
							<button class='btn btn-secondary mt-5 float-left'
								onClick={this.toggleLog}
							>
								<i class='fas fa-plus' /> {showLog ? 'Skjul logg' : 'Ny logg'}
							</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default DogsDetail;
